.automate-section{
  background-color: $bg-color;
  color: white;
  text-align: center;
  padding: 5rem 0;

  @media screen and (max-width: $mobile-screen) {
    padding: 3rem 0;
  }
  
  &__inner{
    max-width: 110rem;
    margin-left: auto;
    margin-right: auto;

    @media screen and (max-width: $desctop-xs) {
      max-width: 80rem;
    }
  }
  
  &__btn{
    width: max-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 2.5rem;
  }
}